<template>
  <el-row :gutter="10" justify="center">
    <el-col :xs="24" :sm="16" :md="12" :lg="10" :xl="7" >
      <div>
        <p style="font-size: var(--el-font-size-extra-large);">IDEA 激活站</p>
        <el-card shadow="never" v-if="component===0" v-loading="loading_input">
          <el-form :model="form" label-position="top" size="large">
            <el-space fill style="margin-bottom: 10px">
              <el-alert type="info" show-icon :closable="false">
                <p>提示：使用该链接前请确保已有 IDEA 账户，如果没有请点击 <el-link href="https://account.jetbrains.com/login" target="_blank">注册</el-link> 前往官网注册。</p>
              </el-alert>
            </el-space>
            <el-form-item>
              <el-input v-model="form.key" placeholder="请输入秘钥兑换激活链接" style="width: 100%"/>
            </el-form-item>
            <el-form-item>
              <div class="right_button">
                <el-button type="primary" @click="onSubmitKey">兑换</el-button>
              </div>
            </el-form-item>
          </el-form>
        </el-card>
        <el-card shadow="never" v-else-if="component===1">
          <el-form :model="form" label-position="top" size="large">
            <el-space fill style="margin-bottom: 10px">
              <el-alert type="info" show-icon :closable="false">
                <p>提示：输入姓氏与名称，激活后可在官网查看定制的激活信息。</p>
              </el-alert>
            </el-space>
            <el-form-item>
              <el-input v-model="form.family_name" placeholder="请输入姓氏" style="width: 100%"/>
            </el-form-item>
            <el-form-item>
              <el-input v-model="form.last_name" placeholder="请输入名" style="width: 100%"/>
            </el-form-item>
            <el-form-item>
              <el-input v-model="form.key" placeholder="请输入秘钥兑换激活链接" style="width: 100%"/>
            </el-form-item>
            <el-form-item>
              <div class="right_button">
                <el-button @click="changeType(0)">普通激活</el-button>
                <el-button type="primary" @click="onSubmit">兑换</el-button>
              </div>
            </el-form-item>
          </el-form>
        </el-card>
        <div v-else-if="component===2">
          <el-card shadow="never">
            <h2>激活链接</h2>
            <el-space>
              <el-link :href="this.link" target="_blank">{{ this.link }}</el-link>
              <el-button
                  type="primary"
                  link
                  class="copy"
                  @click="copyUrl('https://element-plus.gitee.io/zh-CN/component/popover.html')"
              >复制</el-button>
            </el-space>
            <el-space>
              <el-link type="primary" href="https://docs.qq.com/doc/DQ3ZXT3BKZXdDYVJp" target="_blank">查看激活教程</el-link>
              <el-button type="primary" link @click="back_input">后退</el-button>
            </el-space>
          </el-card>
          <textarea id="copyTxt" cols="30" rows="10"></textarea>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import qs from 'qs'
export default {
  name: 'App',

  // components: {
  //   InputKey,
  //   ShowLink,
  //   CustomMade
  // },
  data(){
    return{
      component:0,
      link:'',
      form:{
        family_name:'',
        last_name:'',
        key:'',
      },
      error_count:0,
      loading_input: false
    }
  },
  created() {
    document.body.removeChild(document.getElementById('Loading'))   // 加载页面完后移除加载动画
  },
  methods: {
    onSubmit(){
      console.log('submit!')
    },
    copyUrl(e){
      const copyTxt = document.getElementById("copyTxt");
      copyTxt.value = e;		                //将文本内容赋值到文本框
      copyTxt.select();						//选择文本框中内容
      document.execCommand('copy');			//调用复制文本方法
      this.$message.success("复制成功~");
    },
    changeType(e){
      this.component = e
    },
    back_input(){
      this.loading_input = false
      this.form.key = ''
      this.component = 0
    },
    onSubmitKey(){
      if (this.error_count>4){
        this.$message.error("输入错误秘钥过多！");
        return null
      }
      if (this.form.key === ""){
        this.$message.error("输入错误秘钥！");
        return null
      }
      this.loading_input = true
      var url = "http://129.154.206.249:8011/get_link";
      var that = this;

      that.axios.post(url, qs.stringify({
        'key': that.form.key,
      })).then(function (res) {
        if (res.data.code === 200) {
          // console.log(res);
          that.link = res.data['data'];
          that.component = 2;
          // console.log(that.user.password);
        }else if (res.data.code === 201) {
          that.error_count = that.error_count + 1
          that.loading_input = false
          that.$message.error("输入的秘钥无效，请联系商家！");
        }
      }).catch(function (error) {
        console.log(error);
      });
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#copyTxt {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -20;
}
.right_button{
  width: 100%;
  display: flex;
  justify-content: right;
}

</style>
